<template>
 <view>
	 <input :value="value" :type='type' @input="onGetInput" :placeholder="placeholder" class="input" />
 </view>
</template>
 
<script>
export default {
 data() {
 return {
	name:'11'
  }
 },
 inject:['formItem'],
 props:{
	value:{
		type: String,
		defaul: ""
	},
	type:{
		type: String,
		defaul: 'text'
	},
	placeholder:{
		type: String,
		defaul: ''
	}
 },
 methods:{
	onGetInput(e){
		 this.$emit('input', e.detail.value)
	 }
 }
 
}
</script>

<style scoped>
	.input{
		    background-color: #FFF;
		    border-radius: 4px;
		    border: 1px solid #DCDFE6;
		    -webkit-box-sizing: border-box;
		    box-sizing: border-box;
		    color: #606266;
		    height: 40px;
		    line-height: 40px;
		    outline: 0;
		    padding: 0 15px;
		    width: 100%;
	}
</style>